import { useEffect } from 'react'
import {
  ID_MODAL_ACTION,
  ID_MODAL_HANDLER,
  Modal
} from '../../../../molecules'
import { Column } from '../../../../layout'
import { BigScreen, SmallScreen } from '../../../../../helpers/mediaQueries'
import { AddressModalContent, AddressModalPropsAlvi } from '../AddressModal/AddressModalContent'
import { Size } from '../../../../atoms'
import { AlignItems, JustifyContent } from '../../../../layout/BaseContainer/BaseContainer'
import { Spacer } from '../../../../layout/Spacer/Spacer'
import styles from './AddressModal.module.css'

export const AddressModal = ({
  addressList,
  isDelivery,
  isOpen = true,
  loadingAddress,
  manualAddress,
  placeholderSearch,
  screenHeight,
  showSubtitleList,
  site,
  sla,
  storeList,
  titleModal,
  userAddressList,
  value,
  values,
  addressSelector,
  handleChange,
  handleChangeIsDelivery,
  handleModal,
  onClick,
  onSelect
}: AddressModalPropsAlvi): React.ReactElement => {

  // Calc of custom size by each device less the height fixed of other components
  const customSizeByEachDevice = `calc(${(window.innerHeight * 0.90).toString()}px - 220px)`

  const AddressPropsAlvi = {
    addressList,
    isDelivery,
    loadingAddress,
    manualAddress,
    placeholderSearch,
    showSubtitleList,
    site,
    sla,
    storeList,
    titleModal,
    userAddressList,
    value,
    values,
    addressSelector,
    handleChange,
    handleChangeIsDelivery,
    handleModal,
    onSelect
  }
  const AddressPropsAlviMobile = {
    ...AddressPropsAlvi,
    alignItems: 'start' as AlignItems,
    fontSizeButton: 'base' as Size,
    heightContainerListAddresses: customSizeByEachDevice,
    inputAdornmentEnd: true,
    inputAdornmentStart: false,
    isMobile: true,
    justifyContent: 'start' as JustifyContent,
    marginContainerList: '0',
    maxHeightList: customSizeByEachDevice,
    mobile: true,
    paddingColumList: '0',
    paddingContainer: '0',
    paddingContainerList: '0',
    placeholderSearch: isDelivery ? 'Ingresa una nueva dirección' : 'Busca tu tienda para retirar',
    widthContainerList: '100%'
  }

  const handleDragEnd = (event) => {
    const { clientY } = event.changedTouches[0]
    const targetID = event.target.id
    const minBottomDrag = window.screen.height - (window.screen.height * 0.5)
    const availableClicks = [ID_MODAL_ACTION, ID_MODAL_HANDLER]
    if (clientY > minBottomDrag && availableClicks.includes(targetID)) {
      onClick()
    }
  }

  useEffect(() => {
    window.addEventListener('touchend', handleDragEnd, true)
    return window.removeEventListener('touchend', handleDragEnd)
  }, [])

  return (
    <>
      <BigScreen>
        <Modal
          className={styles.modal_overflow}
          height='100%'
          hiddenX
          isCloseIcon={false}
          isOpen={isOpen}
          maxHeight='550px'
          maxWidth='495px'
          minHeight='550px'
          padding='0'
          toggle={() => { return }}
          toggleOutside={() => { return handleModal() }}
        >
          <AddressModalContent {...AddressPropsAlvi} />
        </Modal>
      </BigScreen>

      <SmallScreen>
        <Modal
          className={styles.modal_overflow}
          fullScreen
          hiddenX
          isCloseIcon={false}
          isOpen={isOpen}
          marginFullScreen={`${window.innerHeight * 0.1}px 0 0 0`}
          maxHeightFullScreen={screenHeight}
          minHeightFullScreen={`calc(${window.innerHeight * 1}px - ${window.innerHeight * 0.1}px)`}
          padding='12px 12px 0 12px'
          toggle={onClick}
          toggleOutside={onClick}
        >
          <Column position='relative'>
            <Spacer.Horizontal size={8} />
            <AddressModalContent {...AddressPropsAlviMobile} />
          </Column>
        </Modal>
      </SmallScreen>
    </>
  )
}
