import React, {
  useEffect,
  useState
} from 'react'
import { getGlobalStyle } from '../../../../../../../../helpers'
import {
  Button,
  Divider,
  Icon,
  Text
} from '../../../../../../../atoms'
import {
  Column,
  Container,
  Row,
  Spacer
} from '../../../../../../../layout'
import { InputAdornment } from '../../../../../../../molecules'
import { LabelInputMap } from '../LabelMapInput'
import { MapModalPropsAlvi } from '../..'
import { CustomInputAddress } from '../CustomInputAddress'
import styles from '../../MapModalContent.module.css'

export const AddressForm = ({
  buttonStatus,
  city,
  commune,
  isMobile,
  showIconValueAddressStreetNumber,
  streetNumberInput,
  values,
  buttonAddressMap,
  handleBlur,
  handleChange,
  onClickClearInput
}: MapModalPropsAlvi) => {
  const [showIconEdit, setShowIconEdit] = useState(false)
  const backgroundColorStatus: string = buttonStatus === 'disabled' ? getGlobalStyle('--color-alvi-neutral-gray-silver') : getGlobalStyle('--color-alvi-primary-blue-gradient')
  const [addressRef, setAddressRef] = useState(false)
  const [addressComments, setAddressComments] = useState(false)

  const handleInputScroll = () => {
    const activeElement = document?.activeElement as HTMLInputElement
    if (activeElement) {
      if (activeElement?.type === 'number') {
        activeElement.blur()
      }
    }
  }

  useEffect(() => {
    window.addEventListener('wheel', handleInputScroll)
    return () => {
      window.removeEventListener('wheel', handleInputScroll)
    }
  }, [])

  return (
    <Row>
      <Column margin='0 0 20px 0' padding='0 17px'>
        <Spacer.Horizontal size={16} />
        {
          streetNumberInput &&
          (
            <Column>
              <Column onMouseEnter={() => { return setShowIconEdit(false) }} onMouseLeave={() => { return setShowIconEdit(true) }}>
                <LabelInputMap label='Número*' />
                <InputAdornment
                  background={getGlobalStyle('--color-background-white')}
                  border='none'
                  color={getGlobalStyle('--color-alvi-text-gray-light')}
                  fontSize='base'
                  height='20px'
                  inputAdornmentEnd={
                    (showIconValueAddressStreetNumber && values?.valueAddressStreetNumber?.length > 0) && (
                      <Icon
                        clickable='pointer'
                        color={getGlobalStyle('--color-icons-gray')}
                        customSize={showIconEdit ? 20 : 12}
                        name={showIconEdit ? 'Edit' : 'Close'}
                        onClick={() => { return !showIconEdit && onClickClearInput('valueAddressStreetNumber') }}
                      />
                    )
                  }
                  inputWidth='100%'
                  min={0}
                  name='valueAddressStreetNumber'
                  onChange={handleChange}
                  onFocus={() => { return setShowIconEdit(false) }}
                  padding='15px  0 15px 0'
                  placeholder='Ingresa el número de la calle'
                  placeholderFocus='placeholder'
                  type='number'
                  value={values.valueAddressStreetNumber}
                  width='auto'
                />
              </Column>
              <Divider
                color={getGlobalStyle('--color-alvi-neutral-gray')}
                thickness={1}
                verticalMargin={0}
              />
              <Spacer.Horizontal size={24} />
            </Column>
          )
        }

        <Column height='84px'>
          <LabelInputMap label='Ciudad' />
          <Row padding='5px 0px 0px'>
            <Text color='neutral-alvi' >
              {city}
            </Text>
          </Row>
          <Spacer.Horizontal size={24} />
        </Column>

        <Column height='84px'>
          <LabelInputMap label='Comuna' />
          <Row padding='5px 0px 0px'>
            <Text color='neutral-alvi' >
              {commune}
            </Text>
          </Row>
          <Spacer.Horizontal size={24} />
        </Column>
        <CustomInputAddress
          handleBlur={handleBlur}
          handleChange={handleChange}
          handleMouseEnter={() => { return setAddressRef(false) }}
          handleMouseLeave={() => { return setAddressRef(true) }}
          inputAdornmentEnd={values?.addressRef?.length > 0}
          label='Departamento, casa u oficina'
          name='addressRef'
          onClickClearInput={onClickClearInput}
          onFocus={() => { return setAddressRef(false) }}
          placeholder='Depto/oficina/casa/piso'
          showIcon={addressRef}
          value={values.addressRef}
        />

        <CustomInputAddress
          handleBlur={handleBlur}
          handleChange={handleChange}
          handleMouseEnter={() => { return setAddressComments(false) }}
          handleMouseLeave={() => { return setAddressComments(true) }}
          inputAdornmentEnd={values?.addressComments?.length > 0}
          label='Instrucciones adicionales'
          name='addressComments'
          onClickClearInput={onClickClearInput}
          placeholder='Agregar una información adicional'
          showIcon={addressComments}
          value={values.addressComments}
        />
      </Column>
      <Container
        absoluteDefault='bottomRight'
        backgroundColor={getGlobalStyle('--color-alvi-base-white')}
        customClassName={styles.content_button_map}
        customHeight='77px'
        position={isMobile ? 'fixed' : 'absolute'}
        width='100%'
      >
        <Button
          background={backgroundColorStatus}
          borderRadius={getGlobalStyle('--border-radius-xl')}
          fontWeight={getGlobalStyle('--font-weight-semibold')}
          label='Confirmar ubicación'
          margin='auto'
          onClick={buttonAddressMap}
          size='md'
          status={buttonStatus}
          width='399px'
        />
      </Container>
    </Row>
  )
}
