import React from 'react'
import { toCapitalize } from '../../../../../../../../utils'
import { RadioSelector, Text } from '../../../../../../../atoms'
import {
  Column,
  Container,
  Spacer
} from '../../../../../../../layout'
import { UserAddressListPropsAlvi } from '../../../../../ModalState/AddressModal/AddressModalContent'
import { getGlobalStyle } from '../../../../../../../../helpers/getGlobalStyle/index'
import { Site } from '../../../../../../../../shared/interfaces/Site'
import { Icons } from '../../../../../../../../assets/SvgComponents/SvgIcons'
import { IAddress } from '../IStoresAlvi'
import styles from './AddressItem.module.css'

interface AddressItemPorps {
  data?: UserAddressListPropsAlvi | IAddress;
  index?: number;
  isDelivery: boolean;
  isStoreLocator?: boolean;
  nameIcon: keyof (typeof Icons);
  selectable?: boolean;
  site?: Site
  addressSelector?: (data: object) => void;
  confirmUserAddress?: () => void;
}

export const AddressItem = ({
  data,
  nameIcon,
  isDelivery,
  isStoreLocator = false,
  selectable = true,
  site,
  addressSelector
}: AddressItemPorps) => {

  const {
    number,
    street,
    state,
    neighborhood,
    selected
  } = data || {}
  
  const isSelected = data?.selected ? `1px solid ${getGlobalStyle('--color-alvi-background-blue')}` : ''

  const handleSelector = () => {
    return selectable ? addressSelector(data) : () => { return }
  }

  return (
    <Container
      alignItems='center'
      border={isSelected}
      borderRadius='12px'
      boxShadow={getGlobalStyle('--box-shadow-2xs')}
      className={styles['cursor-pointer']}
      clickable='pointer'
      justifyContent='center'
      onClick={handleSelector}
      padding='12px'
    >
      <RadioSelector
        checked={selected}
        customClassName={styles.radio_selector}
        isStoreLocator={isStoreLocator}
        nameIcon={nameIcon}
        site={site}
      />

      <Column margin='0px 0px 0px 12px' >
        {isDelivery ? (
          <>
            <Text
              clickable='pointer'
              color='black'
              fontSize='md'
              fontWeight='semibold'
              isFlex
              textAlign='left'
            >
              {street?.length > 0 ? street + ',' : ''} {number}
            </Text>
            <Spacer.Horizontal size={2} />
            <Text
              clickable='pointer'
              color='gray'
              fontSize='md'
              fontWeight='regular'
              isFlex
              textAlign='left'
              truncate={60}
            >
              {`${neighborhood}, ${toCapitalize(state?.toLocaleLowerCase())}`}
            </Text>
          </>
        ) : (
          <>
            <Text
              clickable='pointer'
              color='black'
              fontSize='md'
              fontWeight='semibold'
              isFlex
              textAlign='left'
            >
              {street?.length > 0 && `Alvi ${neighborhood}`}
            </Text>
            <Spacer.Horizontal size={2} />
            <Text
              clickable='pointer'
              color='gray'
              fontSize='md'
              fontWeight='regular'
              isFlex
              textAlign='left'
              truncate={60}
            >
              {`${street}, ${number}, ${toCapitalize(state?.toLocaleLowerCase())}`}
            </Text>
          </>
        )}
      </Column>
    </Container>
  )
}
