import { BigScreen, SmallScreen } from '../../../../../helpers/mediaQueries'
import { Spacer } from '../../../../layout'
import { Modal } from '../../../../molecules'
import { MapModalContent, MapModalPropsAlvi } from '../MapModal/MapModalContent'
import { ErrorMapMessageProps } from './helpers/ErrorMapMessageProps'
import styles from './index.module.css'
import { WrapperMap } from './helpers/WrapperMap'

export const MapModal = ({
  buttonStatus,
  children,
  city,
  commune,
  errorAddress,
  errorAddressType,
  errorMapMessage,
  isOpen = true,
  screenHeight,
  selectedAddress,
  site = 'alvi',
  streetNumberInput,
  titleModal,
  valueAddressRef,
  values,
  back,
  buttonAddressMap,
  handleChange,
  handleModal,
  onClick,
  onClickClearInput
}: MapModalPropsAlvi): React.ReactElement => {
  const MapModalPropsGlobal = {
    buttonStatus,
    city,
    commune,
    isMobile: false,
    selectedAddress,
    site,
    streetNumberInput,
    valueAddressRef,
    values,
    buttonAddressMap,
    handleChange,
    onClickClearInput
  }
  const MapModalPropsGlobalMobile = {
    ...MapModalPropsGlobal,
    inputAdornmentEnd: true,
    isMobile: true,
    spacerMobile: true
  }
  const errorPropsMessage = {
    errorAddress,
    errorAddressType,
    errorMapMessage
  }

  return (
    <>
      <BigScreen>
        <Modal
          className={styles.modal_overflow}
          height='100%'
          hiddenX={true}
          isOpen={isOpen}
          justifyContent='start'
          maxHeight='550px'
          maxWidth='495px'
          minHeight='550px'
          padding='26px 24px 0 24px'
          toggle={() => { return}}
          toggleOutside={handleModal}
        >
          <WrapperMap
            back={back}
            maxHeight='275px'
            site={site}
            titleModal={titleModal}
          >
            {children}
          </WrapperMap>
          <ErrorMapMessageProps {...errorPropsMessage} />
          <MapModalContent {...MapModalPropsGlobal} />
        </Modal>
      </BigScreen>

      <SmallScreen>
        <Modal
          className={styles.modal_overflow}
          fullScreen
          height='100%'
          hiddenX
          isCloseIcon
          isOpen={isOpen}
          marginFullScreen={`${window.innerHeight * 0.1}px 0 0 0`}
          maxHeightFullScreen={screenHeight}
          minHeightFullScreen={`calc(${window.innerHeight * 1}px - ${window.innerHeight * 0.1}px)`}
          padding='26px 12px 0 12px'
          toggle={onClick}
          toggleOutside={onClick}
        >
          <WrapperMap
            back={back}
            site={site}
            titleModal={titleModal}
          >
            {children}
          </WrapperMap>

          <ErrorMapMessageProps {...errorPropsMessage} />
          <Spacer.Horizontal size={12} />
          <MapModalContent {...MapModalPropsGlobalMobile} />
        </Modal>
        
      </SmallScreen>
    </>
  )
}
