import React from 'react'
import { getGlobalStyle } from '../../../../../../../../helpers'
import { Column, Spacer } from '../../../../../../../layout'
import { InputAdornment } from '../../../../../../../molecules'
import { LabelInputMap } from '../LabelMapInput'
import { Icon } from '../../../../../../../atoms'
import { MapModalPropsAlvi } from '../..'
import styles from '../../MapModalContent.module.css'

interface ICustomInputAddress extends MapModalPropsAlvi {
  inputAdornmentEnd?: boolean;
  label: string;
  placeholder: string;
  showIcon?: number | boolean;
  value: string;
  handleMouseEnter?: () => void;
  handleMouseLeave?: () => void;
  onFocus?: () => void;
}
export const CustomInputAddress = ({
  inputAdornmentEnd,
  label,
  name,
  placeholder,
  showIcon,
  value,
  handleBlur,
  handleChange,
  handleMouseEnter = () => { return },
  handleMouseLeave = () => { return },
  onClickClearInput,
  onFocus
}: ICustomInputAddress) => {
  return (
    <>
      <Column
        height='84px'
        onMouseEnter={() => {return handleMouseEnter()}}
        onMouseLeave={() => {return handleMouseLeave()}}
      >
        <LabelInputMap label={label} />
        <InputAdornment
          background={getGlobalStyle('--color-background-white')}
          border='none'
          color={getGlobalStyle('--color-alvi-text-gray-light')}
          customClassName={styles.input_map_modal_ctn}
          fontSize='base'
          height='20px'
          inputAdornmentEnd={
            inputAdornmentEnd &&
            <Icon
              clickable='pointer'
              color={getGlobalStyle('--color-icons-gray')}
              customSize={showIcon ? 20: 12}
              name={showIcon ? 'Edit' : 'Close'}
              onClick={() => { return onClickClearInput(name) }}
            />
          }
          inputWidth='100%'
          name={name}
          onBlur={handleBlur}
          onChange={handleChange}
          onFocus={onFocus}
          padding='15px 0 20px 0'
          placeholder={placeholder}
          placeholderFocus='placeholder'
          value={value}
          width='auto'
        />
      </Column>
      <Spacer.Horizontal size={24} />
    </>
  )
}