import React from 'react'
import { getGlobalStyle } from '../../../../../../helpers'
import {
  Divider,
  Icon,
  Size,
  Text,
  Title,
  ToggleSwitch
} from '../../../../../atoms'
import {
  Column,
  Container,
  Row,
  Spacer
} from '../../../../../layout'
import { AddressItem } from './helpers/AddressItem'
import { AddressListPropsAlvi, ValuesProps } from '../../../StoreLocator'
import { AlignItems, JustifyContent } from '../../../../../layout/BaseContainer/BaseContainer'
import { InputAdornment } from '../../../../../molecules'
import { LoadingAddress } from '../LoaderAddress'
import { ManualAddress } from '../../../../StoreLocator/ModalState/AddressModal/ManualAddress'
import { Site } from '../../../../../../shared/interfaces/Site'
import { Icons } from '../../../../../../assets/SvgComponents/SvgIcons'
import { PickUpStore } from './helpers/PickUpStore'
import { IStore } from './helpers/IStoresAlvi'
import styles from './AddressModalContent.module.css'

export interface UserAddressListPropsAlvi {
  addressName?: string;
  addressType?: string;
  city?: string;
  complement?: string;
  country?: string;
  data?: UserAddressListPropsAlvi
  geoCoordinate?: number[];
  icon?: keyof (typeof Icons);
  iconNotSelected?: string,
  iconSelected?: string,
  isStoreLocator?: boolean;
  id?: string;
  index?: number;
  neighborhood?: string;
  number?: string;
  postalCode?: string;
  receiverName?: string;
  reference?: string;
  selectable?: boolean;
  selected?: boolean;
  site?: Site;
  state?: string;
  street?: string;
  addressSelector?: (data: object) => void;
}
export interface AddressModalPropsAlvi {
  addressList?: AddressListPropsAlvi[];
  alignItems?: AlignItems;
  arrowBack?: boolean;
  currentAddress?: UserAddressListPropsAlvi;
  fontSizeButton?: Size;
  heightContainerListAddresses?: string;
  inputAdornmentEnd?: boolean;
  inputAdornmentStart?: boolean;
  isDelivery?: boolean;
  isMobile?: boolean;
  isOpen?: boolean;
  justifyContent?: JustifyContent;
  loadingAddress?: boolean;
  manualAddress?: boolean;
  marginContainerList?: string;
  maxHeightList?: string;
  paddingColumList?: string;
  paddingContainer?: string;
  paddingContainerList?: string;
  placeholderPickUp?: string;
  placeholderSearch?: string;
  screenHeight?: string;
  showSubtitleList?: boolean;
  site?: Site;
  sla?: string;
  spacer?: boolean;
  storeList?: IStore[];
  titleModal?: string;
  userAddressList?: UserAddressListPropsAlvi[];
  value?: string;
  values?: ValuesProps;
  widthContainerInput?: string;
  widthContainerList?: string;
  addressOnChangeSearch?: (e?: React.ChangeEvent<HTMLInputElement>) => void;
  addressOnClearInput?: (e?: React.ChangeEvent<HTMLInputElement>, refAdornm?: unknown) => void;
  addressSelector?: (data: object) => void;
  changeDeliveryMethod?: () => void;
  deleteAddress?: (data: object, index: number) => void;
  handleChange?: (e?: React.ChangeEvent<HTMLInputElement>) => void;
  handleChangeIsDelivery?: () => void;
  handleModal?: () => void;
  onClick?: () => void;
  onClickClearInputStreetNumber?: () => void;
  onSelect?: (data) => void;
}
export const AddressModalContent = ({
  addressList = [],
  arrowBack = false,
  heightContainerListAddresses = '335px',
  inputAdornmentStart = true,
  isDelivery,
  isMobile,
  loadingAddress,
  marginContainerList = '12px 0 0 0',
  maxHeightList = '347px',
  paddingColumList = '0px 24px 0px 24px',
  paddingContainer = '26px 24px 0px 24px',
  paddingContainerList = '0 0 5px 0',
  placeholderPickUp = 'Busca tu tienda para retirar',
  placeholderSearch = 'Ingresar una nueva dirección',
  showSubtitleList,
  site = 'alvi',
  storeList,
  titleModal,
  userAddressList,
  value,
  values,
  widthContainerInput = '100%',
  widthContainerList = '93%',
  addressSelector,
  handleChange,
  handleChangeIsDelivery,
  handleModal,
  onSelect
}: AddressModalPropsAlvi): React.ReactElement => {
  const valueRange = value?.length > 3
  const queryListLocation = addressList?.length === 0 && userAddressList?.length
  const manualAddress = false
  const queryShowValidate = !valueRange && !!queryListLocation && !loadingAddress
  const paddingContainerAddressList = isMobile ? '' : '0 24px 24px'
  const conditionalArrowBack = arrowBack && !isMobile
  const placeholderSearchLocator = isDelivery ? placeholderSearch : placeholderPickUp
  const unselectedUserAddressList = userAddressList.filter(data => { return data.selected !== true })
  const renderUserAddresList = unselectedUserAddressList.length > 0

  return (
    <>
      <Container customClassName={styles.container} customHeight='100%' >
        <Container padding={paddingContainer}>
          <Column>
            <Row customClassName={styles.containerIconClose}>
              {
                conditionalArrowBack && (
                  <Icon
                    clickable='pointer'
                    customSize={32}
                    name='ArrowBack2'
                    onClick={handleModal}
                    site={site}
                  />
                )
              }
              <Title
                color={getGlobalStyle('--color-icons-black')}
                customFontSize={getGlobalStyle('--font-size-2xl')}
                fontWeight='semibold'
                headingLevel='h5'
                size='large'
                text={titleModal}
                textAlign='center'
              />
              {
                !isMobile && (
                  <Icon
                    clickable='pointer'
                    color={getGlobalStyle('--color-icons-black')}
                    customSize={32}
                    name='Close2'
                    onClick={handleModal}
                  />
                )
              }
            </Row>

            <Divider thickness={0} verticalMargin={12} />
            <Spacer.Horizontal size={12} />

            <ToggleSwitch
              customClass={styles['toggle-switch']}
              fontWeight='semibold'
              leftLabel='Despacho a domicilio'
              onClick={() => { return handleChangeIsDelivery() }}
              rightLabel='Retiro en tienda'
              textPosition='center'
              textSize='md'
              value={isDelivery}
            />

            <Column width={widthContainerInput}>
              <Spacer.Horizontal size={24} />
              <InputAdornment
                autoComplete='off'
                background={getGlobalStyle('--color-background-white')}
                border={`1px solid ${getGlobalStyle('--color-alvi-neutral-gray-dark')}`}
                borderRadius={getGlobalStyle('--border-radius-lg')}
                fontSize='md'
                height='45px'
                inputAdornmentStart={
                  inputAdornmentStart &&
                  <Icon
                    clickable='pointer'
                    color={getGlobalStyle('--color-icons-black')}
                    customSize={16}
                    name='SearchCart'
                    site={site}
                  />
                }
                inputWidth='100%'
                name='search'
                onChange={handleChange}
                onFocus={null}
                padding='8px 12px'
                placeholder={placeholderSearchLocator}
                placeholderFocus='placeholder'
                value={values?.search}
                width='auto'
              />
              <Spacer.Horizontal size={12} />
            </Column>
          </Column>
        </Container>

        {/* List of suggested addresses when typing in the input above */}
        {(addressList?.length > 0) &&
          <>
            <Container
              customClassName={styles.containerAddressList}
              margin={marginContainerList}
              maxHeight={heightContainerListAddresses}
              minHeight={heightContainerListAddresses}
              overflow='scroll'
              padding={paddingColumList}
              width={widthContainerList}
            >
              <Column>
                {addressList.map((data, i) => {
                  const activeIndex = data.activeIndex
                  return (
                    <Column
                      backgroundColor={activeIndex ? getGlobalStyle('--color-alvi-neutral-gray-light') : 'transparent'}
                      borderRadius={activeIndex ? '3px' : '0'}
                      clickable='pointer'
                      key={i}
                      onClick={() => {
                        onSelect(data)
                      }}
                    >
                      <Row>
                        <Container
                          alignItems='center'
                          backgroundColor={getGlobalStyle('--color-alvi-neutral-gray-light')}
                          borderRadius='50%'
                          justifyContent='center'
                          maxHeight='45px'
                          maxWidth='44px'
                          minHeight='45px'
                          minWidth='44px'
                          width='44px'
                        >
                          <Icon
                            clickable='pointer'
                            color={getGlobalStyle('--color-icons-gray')}
                            customSize={24}
                            name={isDelivery ? 'Location' : 'StoreNotSelected' }
                            site={site}
                          />
                        </Container>
                        <Spacer.Vertical size={12} />
                        <Column>
                          <Text
                            clickable='pointer'
                            color='black'
                            fontSize='md'
                            fontWeight='semibold'
                            isFlex
                            textAlign='left'
                          >
                            {data.name}
                          </Text>
                          <Spacer.Horizontal size={2} />
                          <Text
                            clickable='pointer'
                            color='gray'
                            fontSize='md'
                            fontWeight='regular'
                            isFlex
                            textAlign='left'
                          >
                            {data.city}
                          </Text>
                        </Column>
                      </Row>
                      <Spacer.Horizontal size={24} />
                    </Column>
                  )
                })
                }
              </Column>
            </Container>
            <Spacer.Horizontal size={12} />
            {manualAddress && <ManualAddress />}
          </>
        }

        {loadingAddress &&
          <Column padding={paddingColumList}>
            <Container
              customClassName={styles.containerAddressList}
              maxHeight={maxHeightList}
              overflow='auto'
              width='100%'
            >
              <LoadingAddress loadingNumbers={7} />
            </Container>
          </Column>}

        {/* Address stored by user */}
        {
          isDelivery ? (
            <>
              {
                queryShowValidate &&
                <Column
                  justifyContent='between'
                  minHeight={heightContainerListAddresses}
                  padding='0 5px'
                  width='100%'
                >
                  <Container
                    customClassName={styles.containerAddressList}
                    maxHeight={maxHeightList}
                    overflow='auto'
                    padding={paddingContainerAddressList}
                    width='100%'
                  >
                    <Column>
                      <Row>
                        <Column>
                          {
                            userAddressList.filter(item => { return item.selected === true }).map((address, i) => {
                              return (
                                <Column key={address.id} padding='0'>
                                  <AddressItem
                                    addressSelector={addressSelector}
                                    data={address}
                                    index={i}
                                    isDelivery={isDelivery}
                                    isStoreLocator={true}
                                    nameIcon='UbicationAlviSelected'
                                    selectable={false}
                                    site={site}
                                  />
                                </Column>
                              )
                            })
                          }
                        </Column>
                      </Row>

                      <Spacer.Horizontal size={24} />

                      {(renderUserAddresList && queryShowValidate) && (
                        <Row>
                          <Text fontSize='md' fontWeight='semibold'>
                            Direcciones guardadas
                          </Text>
                        </Row>
                      )}
                      <Spacer.Horizontal size={12} />
                      {
                        renderUserAddresList && unselectedUserAddressList.map((data, i) => {
                          return (
                            <Column key={data.id}>
                              <AddressItem
                                addressSelector={addressSelector}
                                data={data}
                                index={i}
                                isDelivery={isDelivery}
                                isStoreLocator={true}
                                nameIcon='UbicationStoreLocatorNotSelected'
                                selectable
                                site={site}
                              />
                            </Column>
                          )
                        })
                      }
                    </Column>
                  </Container>
                </Column>
              }
            </>
          )
            :
            <>
              <PickUpStore
                addressSelector={addressSelector}
                heightContainerListAddresses={heightContainerListAddresses}
                isDelivery={isDelivery}
                maxHeightList={maxHeightList}
                paddingColumList={paddingColumList}
                paddingContainerList={paddingContainerList}
                showSubtitleList={showSubtitleList}
                site={site}
                storeList={storeList}
              />
            </>
        }

      </Container>
    </>
  )
}
